import React from "react";
import OnlineUtils from "eshop/utils/OnlineUtils";


export default class Price extends React.Component {
    render() {
        var price = (this.props.bonusPrice && this.props.bonusPrice != this.props.basePrice) ? this.props.bonusPrice : this.props.basePrice;
        let oldPrice = (this.props.bonusPrice && this.props.bonusPrice != this.props.basePrice) ? this.props.basePrice : "";
        price = OnlineUtils.formatPrice(price);
        oldPrice = oldPrice ? OnlineUtils.formatPrice(oldPrice) : '';
        if (price == oldPrice || !oldPrice)
            oldPrice = '';
        price = price && price.split(',');
        var integerPricePart = price && price[0];
        var fractionPricePart = price && price[1];
        return (

            <div>
                    <span className="opl-price-v2 opl-price-v2--l" data-price={price}>
                        <span className="opl-price-v2__part">
                            <span className="opl-price-v2__whole  g-brand1-c">{integerPricePart}</span>
                        </span>
                        <span className="opl-price-v2__part">

                            <span className="opl-price-v2__decimal g-brand1-c">{fractionPricePart}</span>
                            <span className="opl-price-v2__suffix">zł/mies.</span>
                        </span>
                    </span>
                {
                    oldPrice &&
                    <p className="u-padding-top u-font-bold g-gray5-c u-text-line-through">{oldPrice + " zł/mies."}</p>
                }
            </div>
        )
    }
}